import {
  EditOutlined,
  FilterOutlined,
  PlusCircleOutlined,
  SearchOutlined,
  SyncOutlined,
  TeamOutlined,
} from "@ant-design/icons";
import { Button, Col, Form, PageHeader, Radio, Grid, Row, Select, Tooltip } from "antd";
import SearchFilterDropdown from "components/shared-components/SearchFilter";
import TableComponent from "components/shared-components/TableComponent/Index";
import RestrictedContainer from "components/util-components/RestrictedContainer";
import {
  ACTIONS,
  ACTIVE_STATUS,
  ALL_STATUS,
  CHILDREN,
  COMPLETED_STATUS,
  DATE_FORMAT,
  LIGHT_NAVY_BLUE_COLOR,
  NAVIGATION_ASSETS,
  NAVIGATION_ASSETS_RIGHTS,
  SORT_DIRECTIONS,
} from "constants/CommonConstants";
import { ADMIN_ROLES } from "constants/ManageEmployeeConstants";
import {
  ADD_PROJECT_TITLE,
  DATA_TABLE_COLUMNS,
  GROUP_PROPOSED_ACTIVE_STATUS,
  MANAGE_PROJECT_TITLE,
} from "constants/ManageProjects";
import useIntlMessage from "hooks/useIntlMessage";
import moment from "moment";
import { useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { showLoader } from "redux/actions/Loader";
import domainService from "services/DomainService";
import projectService from "services/ProjectService";
import Utils from "utils";
import EditProjectForm from "./EditManageProject";
import ViewTeam from "./teams/view-team/ViewTeam";
const { useBreakpoint } = Grid;
function ManageProjects() {
  const dispatch = useDispatch();
  const isMobile = !Utils.getBreakPoint(useBreakpoint()).includes("sm");
  let searchInput = useRef(null);
  const [show, setShow] = useState(false);
  const [editProject, setEditProject] = useState(null);
  const [isEdit, setIsEdit] = useState(false);
  const [value, setValue] = useState(ACTIVE_STATUS.value);
  const [filtered, setFiltered] = useState([]);
  const [filteredInfo, setFilteredInfo] = useState({});
  const [sortedInfo, setSortedInfo] = useState({
    // order: "ascend",
    // columnKey: STATUS.value,
  });
  const [showViewTeamModal, setShowViewTeamModal] = useState(false);
  const [selectedProject, setSelectedProject] = useState(null);
  const [domains, setDomains] = useState([]);
  const [selectedAccount, setSelectedAccount] = useState("");
  const [accountList, setAccountLists] = useState([]);
  const getMessage = useIntlMessage();
  const role = useSelector(state => state?.myTeams?.userInfo?.role);
  const { Option } = Select;
  const [form] = Form.useForm();

  useEffect(() => {
    const getActiveAccountListByEmpId = async () => {
      try {
        const accountList = await projectService.getActiveAccountListByEmpId();
        setAccountLists(accountList);
      } catch (e) {
        console.error("Error while getting the Account List");
      }
    };
    getActiveAccountListByEmpId();
  }, []);

  const getProjectListsByAccountId = async (accountName, status) => {
    try {
      dispatch(showLoader(true));
      const projectList = await projectService.getList({ accountId: accountName });
      status === ALL_STATUS.value
        ? setFiltered(projectList)
        : setFiltered(projectList?.filter(el => el.status.toLowerCase() === status.toLowerCase()));
    } catch (e) {
      console.error("Unable to populate project list", e);
    } finally {
      dispatch(showLoader(false));
    }
  };

  /** Refetch projects list after "ADD" OR "UPDATE" Project. */
  const refetchProjectList = async () => {
    if (selectedAccount) {
      setValue(ACTIVE_STATUS.value);
      getProjectListsByAccountId(selectedAccount, ACTIVE_STATUS.value);
      setSortedInfo({});
      setFilteredInfo({});
    }
  };

  const onChangeStatus = e => {
    setValue(e.target.value);
    form
      .validateFields()
      .then(() => {
        getProjectListsByAccountId(selectedAccount, e.target.value);
      })
      .catch(e => {
        console.error(e);
      });
  };

  const handleSearch = (selectedKeys, confirm, dataIndex) => {
    confirm();
  };

  const handleChange = (pagination, filters, sorter) => {
    setFilteredInfo(filters);
    setSortedInfo(sorter);
  };

  const handleReset = (clearFilters, confirm) => {
    clearFilters();
    confirm();
  };

  const handleShowAdd = () => {
    setShow(true);
    setIsEdit(false);
  };

  const handleEditProject = record => {
    setShow(true);
    setIsEdit(true);
    setEditProject({ ...record });
  };

  const handleCancelEditAccount = () => {
    setShow(false);
    setIsEdit(false);
    setEditProject(null);
  };

  const fetchProjectResourcesAndDomains = async project => {
    console.log(project.domainId)
    const domains = await domainService.getSubdomiansOfDomain(project.domainId[0]);
    console.log(domains);
    setDomains(domains);
  };

  const enableViewTeam = async record => {
    console.log(record)
    try {
      setShowViewTeamModal(true);
      setSelectedProject(record);
      await fetchProjectResourcesAndDomains(record);
    } catch (error) {
      console.error("something went wrong", error);
    }
  };

  const handleCancel = e => {
    setShowViewTeamModal(false);
    setSelectedProject(null);
  };

  const getColumnSearchProps = field => {
    let filterObj = { filterIcon: filtered => <SearchOutlined color={filtered ? LIGHT_NAVY_BLUE_COLOR : undefined} /> };
    if (field.type === "select") {
      if (!Utils.isEmpty(field.options)) {
        filterObj.filters = field.options.map(option => ({
          text: getMessage(option.label),
          value: option.value,
        }));
      }
      filterObj.filterIcon = filtered => <FilterOutlined color={filtered ? LIGHT_NAVY_BLUE_COLOR : undefined} />;
      filterObj.onFilter = (value, record) => record[field.key] === value;
    } else {
      filterObj.filterDropdown = ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
        <SearchFilterDropdown
          setSelectedKeys={setSelectedKeys}
          selectedKeys={selectedKeys}
          confirm={confirm}
          clearFilters={clearFilters}
          handleSearch={handleSearch}
          field={field}
          dataIndex={field.key}
          searchInput={searchInput}
          handleReset={handleReset}
          dataSource={filtered}
        />
      );
      filterObj.onFilter = (value, record) => {
        if (field.type === "date") {
          const _value = value.format(DATE_FORMAT).toString();
          const timeStampToDate = moment(record[field.key]).format(DATE_FORMAT).toString();
          return timeStampToDate.toString().toLowerCase().includes(_value.toLowerCase());
        }
        return record[field.key]?.toString().toLowerCase().includes(value.toLowerCase());
      };
      filterObj.onFilterDropdownVisibleChange = visible => {
        if (visible) {
          setTimeout(() => searchInput && searchInput.select && searchInput.select());
        }
      };
    }
    return filterObj;
  };

  const dataColumns = Object.values(DATA_TABLE_COLUMNS).map(field => ({
    title: (
      <Tooltip title={getMessage(field.label)} placement="top">
        <span>{getMessage(field.label)}</span>
      </Tooltip>
    ),
    dataIndex: field.key,
    key: field.key,
    sorter: (a, b) => Utils.antdTableSorter(a, b, field.key),
    sortDirections: SORT_DIRECTIONS,
    filteredValue: filteredInfo[field.key] || null,
    ellipsis: true,
    width: field.width || null,
    sortOrder: sortedInfo.columnKey === field.key ? sortedInfo.order : null,
    ...getColumnSearchProps(field),
    render: (_, record) => {
      let _value = "";
      if (field.type === "date") {
        _value = record[field.key] ? moment(record[field.key]).format(DATE_FORMAT) : "";
      } else if (field.type === "list") {
        _value = record[field.key].join(", ");
      } else {
        _value = record[field.key];
      }
      return (
        <Tooltip title={_value} placement="top">
          <span>{_value}</span>
        </Tooltip>
      );
    },
  }));

  const columns = [
    ...dataColumns,
    {
      fixed: isMobile ? "" : "right",
      title: ACTIONS.text,
      key: ACTIONS.value,
      width: 120,
      render: (_, record) => {
        return (
          <>
            <Tooltip title={getMessage("component.projectallocation.teamdetails.label")}>
              <Button className="mr-2" onClick={() => enableViewTeam(record)} size="small">
                <TeamOutlined />
              </Button>
            </Tooltip>
            <RestrictedContainer assetId={NAVIGATION_ASSETS.ManageProject} assetRight={NAVIGATION_ASSETS_RIGHTS.UPDATE}>
              {GROUP_PROPOSED_ACTIVE_STATUS.includes(record.status) && !record.isBenchProject ? (
                <Tooltip title={getMessage("common.label.edit")}>
                  <Button onClick={() => handleEditProject(record)} size="small">
                    <EditOutlined />
                  </Button>
                </Tooltip>
              ) : null}
            </RestrictedContainer>
          </>
        );
      },
    },
  ];

  const onChangeAccount = account => {
    setSelectedAccount(account);
    getProjectListsByAccountId(account, value);
  };

  return (
    <>
      <Row align="middle" gutter={[20, 20]} className="mb-2">
        <Col xs={24} md={12} lg={12} xl={6}>
          <PageHeader
            className="p-2 pl-2"
            title={MANAGE_PROJECT_TITLE.text}
            subTitle={
              <Button className="ml-4" onClick={refetchProjectList} disabled={!selectedAccount}>
                <SyncOutlined shape="circle" />
              </Button>
            }
          />
        </Col>

        <RestrictedContainer assetId={NAVIGATION_ASSETS.ManageProject} assetRight={NAVIGATION_ASSETS_RIGHTS.CREATE}>
          <Col flex={"auto"} className="text-right">
            <Button type="primary" onClick={handleShowAdd}>
              <PlusCircleOutlined className="mr-2" />
              {ADD_PROJECT_TITLE.text}
            </Button>
          </Col>
        </RestrictedContainer>
      </Row>

      <Row align="middle" gutter={[20, 20]} className="mb-2" justify="end">
        <Col xs={24} md={12} lg={8} xl={8}>
          <Form name="basic" form={form}>
            <Form.Item
              name="manage-project-select-account"
              label={getMessage("common.label.select.account")}
              rules={[{ required: true, message: "Select Account" }]}
              className="mb-0"
            >
              <Select
                className="w-100"
                placeholder={getMessage("common.label.select.account")}
                name={"manage-project-select-account"}
                id="manage-project-select-account"
                onChange={onChangeAccount}
                showArrow
                showSearch
                optionFilterProp={CHILDREN}
              >
                {accountList?.map(account => {
                  return (
                    <Option key={account?.accountId} value={account?.accountId}>
                      {account?.accountName}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Form>
        </Col>

        {!Utils.isEmpty(role) &&
        (role === ADMIN_ROLES.ADMIN.key || role === ADMIN_ROLES.SUPER_ADMIN.key || role === "RMG") ? (
          <Col xs={24} md={12} lg={6} xl={6} className="text-xl-right text-md-right text-xs-right">
            <Radio.Group onChange={onChangeStatus} value={value}>
              <Radio value={ACTIVE_STATUS.value}>{ACTIVE_STATUS.text}</Radio>
              <Radio value={COMPLETED_STATUS}>{COMPLETED_STATUS}</Radio>
              <Radio className="m-0" value={ALL_STATUS.value}>
                {ALL_STATUS.text}
              </Radio>
            </Radio.Group>
          </Col>
        ) : null}
        {/* <Col className="mb-2 text-xl-right text-md-right" xs={24} md={12} lg={3} xl={3}>
          <Button type="primary" onClick={handleOnSearch}>
            <SearchOutlined className="mr-2" />
            {getMessage("component.managedomains.search.text")}
          </Button>
        </Col> */}
      </Row>

      <TableComponent
        rowKey={record => record.id?.timestamp}
        columns={columns}
        data={filtered}
        scroll={{ x: 1300 }}
        handleChange={handleChange}
        tableSize="small"
        showDropdown={false}
        showSorterTooltip={false}
      />

      {showViewTeamModal && (
        <ViewTeam
          selectedProject={selectedProject}
          handleCancel={handleCancel}
          visible={showViewTeamModal}
          toggleViewTeamModal={setShowViewTeamModal}
          projectDomains={domains}
        />
      )}

      {show && (
        <EditProjectForm
          show={show}
          isEdit={isEdit}
          onCancel={() => setShow(false)}
          setShow={setShow}
          editProject={editProject}
          accountsList={accountList}
          cancelEdit={handleCancelEditAccount}
          refetchProjectList={() => {
            if (selectedAccount) {
              refetchProjectList();
            }
          }}
        />
      )}
    </>
  );
}

export default ManageProjects;
